import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import { ITableSelection } from '../types';
import createTableSelectionForFocusedCell from './createTableSelectionForFocusedCell';

export const getToolbarTableSelection = (controller: Controller, table: Table) => {
  const tblSelections = controller.query(
    'viewTableSelections',
  );
  let tbSel: ITableSelection | undefined | null;
  if (tblSelections) {
    tbSel = tblSelections.find(
      (ts) => ts.key === table.key,
    );
  }
  if (!tbSel) {
    tbSel = createTableSelectionForFocusedCell(controller.value, table);
  }
  return tbSel;
};
